.about__container {
  display: grid;
  grid-template-columns: 35% 60%;
  gap: 6%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--clr-champagne),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  aspect-ratio: 1/1;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}

.about__me-image:hover {
  transform: rotate(0deg);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 1.5rem;
}

.about__card {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: var(--clr-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem 4rem;
  transition: var(--transition);
}

.about__card:hover {
  background: transparent;
  border-color: var(--clr-champagnelight);
  cursor: default;
}

.about__icon {
  color: var(--clr-champagne);
  font-size: 1.4rem;
  margin-bottom: 1.4rem;
}

.about__card h5 {
  font-size: 0.95rem;
}

.about__card small {
  font-size: 0.7rem;
  color: var(--clr-champagnelight);
}

.about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--clr-whiteishlight);
}

/* MEDIA QUERY MEDIUM DEVICES */
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content p {
    margin: 1rem 0 1.5rem;
  }
}

/* MEDIA QUERY SMALL DEVICES */
@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    display: none;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem;
  }
}
